*{box-sizing:border-box;margin:0px;padding:0px}.actualitéMain{width:100%;min-height:300px;background-color:#fff;padding:100px 50px 50px 50px;display:flex;justify-content:center;flex-direction:row}.leftsite{width:20%;margin-right:40px;display:block}.midle{width:40%}.rightsite{width:20%;margin-left:40px}.big-image-ctn{width:100%;height:auto;overflow:hidden;position:relative}.big-img{width:100%;height:auto}.title-container{width:100%;height:auto;z-index:5;background-color:rgba(0,0,0,.5);padding:25px;position:absolute;bottom:0px;color:#fff}.titre{font-size:30px;font-family:"Anton",sans-serif}.sous-titre{font-size:17px;font-family:"Roboto Condensed",sans-serif}.article-texte{color:#000;font-family:"Quicksand",sans-serif;text-align:justify;margin-top:20px}.block-title{font-family:"Roboto",sans-serif;color:#1b5531;font-size:20px}form{margin-top:10px;width:100%;height:auto}form input{width:100%;padding:10px;background-color:rgba(63,63,62,.3);margin-top:7px;border:none;outline:none}form button{border:none;padding:10px;background-color:#1b5531;width:100%;color:#fff;cursor:pointer;margin-top:10px;transition:.7s}form button:hover{color:#3f3e3e;background-color:#ead01a;transition:.7s}.ads{width:100%;height:auto;margin-top:15px}.ads-img{width:100%;height:auto;position:relative}.allads{border:double 5px #1b5531;color:#1b5531;display:flex;font-family:"Tajawal",sans-serif;z-index:20}.box2{margin-top:10px}@media screen and (max-width: 700px){.actualitéMain{padding:100px 30px 30px 30px;display:flex;justify-content:center;flex-direction:column}.leftsite{display:none}.midle{width:100%}.rightsite{width:100%;margin:0px;padding-top:50px}.title-container{padding:25px 10px 10px 10px}.titre{font-size:20px;font-family:"Anton",sans-serif}.sous-titre{font-size:15px;font-family:"Roboto Condensed",sans-serif}}